/* Rimuove la sottolineatura dai link all'interno della card */
.card a {
    text-decoration: none !important;
    color: inherit !important; /* Mantiene il colore del testo normale */
}

/* Assicura che anche quando il link è attivo o hoverato non venga sottolineato */
.card a:hover,
.card a:focus,
.card a:active {
    text-decoration: none !important;
    color: inherit !important;
}

/* Effetto hover sulla card: aumenta l'ombreggiatura */
.card:hover {
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.8) !important;
    transition: box-shadow 0.3s ease-in-out;
}
